/* General Portfolio Section */
.portfolio {
  text-align: center;
  padding: 20px 10%;
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background-color 0.3s, color 0.3s;
}

.project-detail {
  margin-bottom: 30px;
}

.project-detail h3 {
  text-align: center;
  color: var(--text-color);
  margin: 7% 0;
  font-size: 1.2rem;
}

.project-detail p {
  font-size: 1.1rem;
  color: var(--text-color);
}

/* Portfolio Navigation Bar */
.portfolio-nav {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.portfolio-nav button {
  padding: 8px 15px;
  border: none;
  background: var(--accent-color);
  color: var(--bg-color);
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

.portfolio-nav button:hover {
  background: var(--secondary-color);
}

.portfolio-nav .active {
  background: var(--secondary-color);
  font-weight: bold;
}

/* Portfolio Grid */
.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  justify-content: center;
}

/* Project Cards */
.project-card {
  border: 1px solid var(--border-color);
  border-radius: 10px;
  padding: 15px;
  text-align: center;
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-card:hover {
  transform: translateY(-5px);
}

/* Project Image */
.project-image {
  width: 100%;
  height: 50%;
  border-radius: 8px;
}

/* Project Description */
.description h3 {
  font-size: 1.2rem;
  margin: 10px 0;
  color: var(--text-color);
}

.description p {
  font-size: 1rem;
  color: var(--text-color);
}

.description a {
  display: inline-block;
  margin-top: 10px;
  padding: 8px 12px;
  background: var(--accent-color);
  color: var(--bg-color);
  text-decoration: none;
  border-radius: 5px;
  transition: 0.3s;
}

.description a:hover {
  background: var(--secondary-color);
}

.theme-toggle-btn {
  padding: 0.5rem 1rem;
  border: none;
  background-color: var(--accent-color);
  color: var(--bg-color);
  cursor: pointer;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s, color 0.3s;
}

@media (max-width: 768px) {
  .portfolio-nav {
    gap: 5px;
  }

  .portfolio-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

